    background-color: rgb(0 213 81 / var(--tw-bg-opacity));
}

.hover\:bg-secondary:hover {
    --tw-bg-opacity: 1;
    background-color: rgb(255 213 13 / var(--tw-bg-opacity));
}

.hover\:text-secondary:hover {
